<template>
    <div class="content">
        <Navtop />
        <div class="ma">
            <div class="search">
                <Icon class="icon" type="ios-search-outline" size = '30'/>
                <input type="text" v-model="seach" @input="sea" placeholder="请输入你想搜索的商品">
            </div>
            <div class="seama">
                <div v-for="index in 4" class="sea" v-show="dis[index-1]" :key="index" @click='goDetails'>
                    <img :src="img[index-1]" alt="">
                    <h4>{{title[index-1]}}</h4>
                    <p>{{size[index-1]}}</p>
                    <small>价格:￥ {{price[index-1]}}</small>
                </div>
            </div>
        </div>
        <Navlist />
    </div>    
</template>

<script>
import Navlist from '../components/navList'
import Navtop from '../components/navtop'

export default {
    mounted(){
        this.downdate();
    },
    name:"categorzie",
    data(){
        return{
            img:[null],
            title:[null],
            price:[null],
            size:[null],
            seach:'',
            dis:[true,true,true,true]
        }
    },
    components:{
        Navlist,
        Navtop
    },
    methods:{
        goDetails(){
            this.$router.push({
                name:'details',
                query:{msg:this.val}
            })
        },
        sea(){
            let regex = new RegExp([this.seach]);
            for(let i = 0 ; i < this.title.length;i++){
                if(!regex.test(this.title[i])){
                    this.dis[i] = false;
                }
                if(this.seach == ''){
                    for(let j = 0 ; j<this.dis.length;j++){
                        this.dis[j] = true;
                    }
                }
            }
        },
        downdate(){
            Bmob.initialize("0319b91e2178a41cbd7c7683126ed4cd", "3afd32e8095f62c049de343910a9473f");
            var Goods = Bmob.Object.extend("goods");
            var query = new Bmob.Query(Goods);
            query.find({
            success: (results) =>{
                let imgstr = [];
                let titlestr = [];
                let pricestr  = [];
                let sizestr = [];
                for (var i = 0; i < results.length; i++) {
                    var object = results[i];
                    imgstr.push(object.get('imgurl'));
                    titlestr.push(object.get('name'));
                    pricestr.push(object.get('price'));
                    sizestr.push(object.get('size'));
                }
                this.img= imgstr;
                this.title = titlestr;
                this.price = pricestr;
                this.size = sizestr;
            },
            error: function(object, error) {
                alert("query object fail");
            }
            });
        }
    },
}
</script>

<style scoped>
.sea{
    margin-top: 20px;
    clear: both;
    width: 100%;
    border-bottom: 1px solid gray;
    height: 150px; 
    position: relative;
}
.sea img{
    float: left;
    height:100px;
    margin:10px 30px 0 20px;
}
.sea small{
    font-size: 18px;
    color: rgb(110, 81, 27);
    position: absolute;
    left: 33%;
    bottom: 10px;;
}
.sea p{
    color: rgb(170, 172, 172);
}
.sea h4{
    color: rgb(37, 137, 231);
    font-size: 15px;
    margin-bottom: 10px;
}
.search {
    position: relative;
    margin-top: 20px;
}
.search .icon{
    position: absolute;
    left: 6%;
    top: 50%;
    transform: translateY(-50%)
}
.search input{
    margin-left: 5%;
    width: 90%;
    line-height: 30px;
    outline: none;
    border-radius: 20px;
    border: 1px solid gray;
    text-indent: 2em;
    font-size: 18px;
}
</style>



