<template>
    <div class="content">
       <Navtop :goBack='true' />
        <div class="ma">
            <form action="">
                <p><span>昵称</span><input type="text" placeholder="请输入昵称" v-model="name"></p>
                <p><span>签名</span><input type="text" placeholder="请输入签名" v-model="style"></p>
                <p>
                    <span>性别</span>
                    <small class="btn">
                        <button type="button" v-bind:class="{ sty: isBoy }" @click='boy'>男</button>
                        <button type="button" v-bind:class="{ sty: isGirl }" @click='girl'>女</button>
                        <button type="button" v-bind:class="{ sty: isXxx }" @click='xxx'>保密</button>
                    </small>
                </p>
                <p><span>出生年月</span><input type="date" v-model="birthday"></p>  
            </form>
            <button class="change" type="button" @click='okChange'>确认修改</button>
        </div>
    </div>    
</template>

<script>
import Navtop from '../components/navtop'

export default {
    name:"set",
    data(){
        return{
            gender:'',
            name:'',
            style:'',
            birthday:'',
            isBoy:true,
            isGirl:false,
            isXxx:false,
        }
    },
    mounted(){
        this.getSet();
        this.boy();
        this.girl();
        this.xxx();
    },
    methods:{
        boy(){
            if(this.gender!='男'){
                this.isBoy = true;
                this.isGirl = false;
                this.isXxx = false;
                this.gender = '男';
            }
        },
        girl(){
            if(this.gender!='女'){
                this.gender = '女';
                this.isGirl = true;
                this.isBoy = false;
                this.isXxx = false;
            }
        },
        xxx(){
            if(this.gender!='保密'){
                this.gender = '保密'
                this.isXxx = true;
                this.isGirl = false;
                this.isBoy = false;
            }
        },
        okChange(){
            let self = this;
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_set"+admin);
            var gameScore = new GameScore();
            gameScore.set("name", this.name);
            gameScore.set("gender", this.gender);
            gameScore.set("style", this.style);
            gameScore.set('birthday',this.birthday);
            gameScore.save(null, {
            success: function(gameScore) {
                alert('修改成功');
                self.$router.push({name:'mian'});
            },
            error: function(gameScore, error) {
                alert('添加数据失败，返回错误信息：' + error.description);
            }
            });
        },
        getSet(){
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_set"+admin);
            var query = new Bmob.Query(GameScore);
            query.find({
            success: (results)=> {
                for (var i = results.length-1; i >=0; i--) {
                var object = results[results.length-1];
                    this.name = object.get('name');
                    this.gender = object.get('gender');
                    this.birthday = object.get('birthday');
                    this.style = object.get('style');
                }
            },
            error: function(error) {
                alert('第一次进入，请设置你的个人信息');
            }
            });
        }
    },
    components:{
        Navtop
    }
}
</script>

<style scoped>
.sty{
    background: green!important;
    color: white;

}
form p{
    width: 100%;
    height: 40px;
    margin-top: 20px;
    border-bottom: 1px solid rgb(202, 202, 202);
    position: relative;
}
form span{
    font-size: 18px;
    margin-left: 15px;
    color: rgb(152, 158, 158)
}
form input{
    font-size: 20px;
    outline: none;
    border: none;
    position: absolute;
    left: 30%;
    color: gray;
}
.btn{
    font-size: 0;
    position: absolute;
    left: 30%;
    top: -10px;
    border: 1px solid gray;
    border-radius: 10px;
}
.btn button{
    width: 50px;
    line-height: 35px;
    font-size: 20px;
    border: none;
    background: white;
    outline: none;
}
.btn :first-child{
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border-right: 1px solid gray;
}
.btn :last-child{
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-left: 1px solid gray;
}
.change{
    margin-top: 50px;
    width: 80%;
    height: 45px;
    background: green;
    color: white;
    font-size: 18px;
    outline: none;
    border: none;
    border-radius: 10px;
    margin-left: 10%;
}
</style>



