<template>
    <div class="content">
        <Navtop />
        <div class="ma">
            <div class="userImg">
                <div class="userhead">
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/927e314c40402fbe80bdf7f9c01ed028.png" alt="">
                </div>
                <p>{{name}}</p>
                <small>{{style}}</small>
            </div>
            <div class="set">
                <div>
                    <button @click='goSet'>设置信息</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                <div>
                    <button @click="goCite">收货地址</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
            </div>
            <div class="sell">
                <div v-for="items in sell" :key="items">
                    <button type="button">{{items}}</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
            </div> 
            <div class="goods">
                <div v-for="(item, index) in goods" :key="index">
                    <button type="button">{{item}}</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
            </div>
            <button class="btn" @click='goLogin'>退出登录</button>
            
        </div>
        <Navlist />
    </div>    
</template>

<script>
import Navlist from '../components/navList'
import Navtop from '../components/navtop'


export default {
    name:"personal",
    data(){
        return{
            style:"请输入你的个性签名",
            name:'追花者',
            sell:['我的订单','我的评价'],
            goods:['商品收藏','优惠卷','浏览记录'],
        }
    },
    components:{
        Navlist,
        Navtop
    },
    mounted(){
        this.isLogin();
        this.dowmName();
    },
    methods:{
        goCite(){
            this.$router.push({
                name:'place',
            })
        },
        goLogin(){
            sessionStorage.isLogin = '';
            this.$router.push({
                name:'login',
            })
        },
        goSet(){
            this.$router.push({
                name:'set',
            })
        },
        isLogin(){
                if(!(sessionStorage.isLogin)){
                    this.$router.push({
                    name:'login',
                })
            }
        },
        dowmName(){
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_set"+admin);
            var query = new Bmob.Query(GameScore);
            query.find({
            success: (results)=> {
                for (var i = results.length-1; i >=0; i--) {
                var object = results[results.length-1];
                    this.name = object.get('name');
                    this.style = object.get('style');
                }
            },
            error: function(error) {
            }
            });
        }
    }
}
</script>

<style scoped>
.ma{
    background: rgb(226, 221, 221);
}
.userImg{
    background:  rgb(255, 102, 0);
    height: 25%;
    position: relative;
}
.userImg img{
    width: 70px;
    height: 70px;
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
}
.userImg p{
    color: white;
    font-size: 20px;
    position: absolute;
    left: 30%;
    top: 50%;
    transform: translateY(-50%);
    font-family:'楷体'
}
.userImg small{
    position: absolute;
    left: 30%;
    top: 60%;
    color:white;
    font-size: 15px;
}
.set, .goods, .sell{
    background: white;
    margin-top: 10px;
}
.set div, .goods div, .sell div{
    position: relative;
    border-bottom: 1px solid gray;
    line-height: 50px;
}
.set button, .goods button, .sell button{
    outline: none;
    width: 93%;
    position: absolute;
    text-align: left;
    left: 7%;
    background: white;
    border: none;
    font-size: 18px;
    font-weight: lighter;
    font-family:cursive;
    color: rgb(73, 70, 70);
}
.icon{
    transform: translateX(350px);
}
.btn{
    margin-top: 20px;
    width: 80%;
    height: 45px;
    background: orange;
    color: white;
    font-size: 18px;
    outline: none;
    border: none;
    border-radius: 10px;
    margin-left: 10%;
}
</style>



