<template>
    <div class="content">
        <Navtop :goBack='true' />
        <div class="ma">
            <form action="">
                <p><span>姓名</span><input type="text" placeholder="请输入昵称" v-model="name"></p>
                <p><span>电话号码</span><input type="text" placeholder="请输入签名" v-model="phone"></p>
                <p><span>邮政编码</span><input type="text" placeholder="请输入昵称" v-model="mail"></p>
                <p><span>详细地址</span><input type="text" placeholder="请输入签名" v-model="cite"></p>
            </form>
            <button type="button" class="change" @click="okChange">修改地址</button>
        </div>
        <Navlist />
    </div>    
</template>

<script>
import Navlist from '../components/navList'
import Navtop from '../components/navtop'

export default {
    name:"",
    data(){
        return{
            name:'',
            phone:'',
            mail:'',
            cite:'',
        }
    },
    mounted(){
        this.getCite();
    },
    methods:{
        okChange(){
            let self = this;
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_cite"+admin);
            var gameScore = new GameScore();
            gameScore.set("name", this.name);
            gameScore.set("phone", this.phone);
            gameScore.set("mail", this.mail);
            gameScore.set('cite',this.cite);
            gameScore.save(null, {
            success: function(gameScore) {
                alert('修改成功');
                self.$router.push({name:'mian'});
            },
            error: function(gameScore, error) {
                alert('添加数据失败，返回错误信息：' + error.description);
            }
            });
        },
        getCite(){
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_cite"+admin);
            var query = new Bmob.Query(GameScore);
            query.find({
            success: (results)=> {
                for (var i = results.length-1; i >=0; i--) {
                var object = results[results.length-1];
                    this.name = object.get('name');
                    this.phone = object.get('phone');
                    this.cite = object.get('cite');
                    this.mail = object.get('mail');
                }
            },
            error: function(error) {
            }
            });
        }
    },
    components:{
        Navlist,
        Navtop
    }
}
</script>

<style scoped>
form p{
    width: 100%;
    height: 40px;
    margin-top: 20px;
    border-bottom: 1px solid rgb(202, 202, 202);
    position: relative;
}
form span{
    font-size: 18px;
    margin-left: 15px;
    color: rgb(152, 158, 158)
}
form input{
    font-size: 20px;
    outline: none;
    border: none;
    position: absolute;
    left: 30%;
    color: gray;
}
.change{
    margin-top: 50px;
    width: 80%;
    height: 45px;
    background: green;
    color: white;
    font-size: 18px;
    outline: none;
    border: none;
    border-radius: 10px;
    margin-left: 10%;
}
</style>



