<template>
    <div class="content">
        <Navtop :goBack='true' />
        <div class="ma">
            <h3>商品信息确认</h3>
            <div class="price">
                <h4>价格信息</h4>
                <p>商品总价 <span>{{pric}}</span></p>
                <p>配送费<span>10.00</span></p>
                <big>订单总价<span>{{pric+10}}</span></big>
            </div>
            <div class="cite">
                <h4>收货信息</h4>
                <p>收货地址<span>{{cite}}</span></p>
                <p>收货人<span>{{name}}</span></p>
                <p>邮编<span>{{mail}}</span></p>
                <p>电话<span>{{pho}}</span></p>
                <div class="btn">
                    <button type="button" @click="changeCite">修改收货人信息</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                
            </div>
            <h3>选择支付方式</h3>
            <div class="pay">
                <div>
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/96b0182b40f052d4801d307cb94194de.png" alt="">
                    <button>微信支付</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                <div>
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/89ff890d40d1db9c80f7c99f662fc757.png" alt="">
                    <button>支付宝</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                <div>
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/06330d0f40289d0b8052aa6622ae8acb.png" alt="">
                    <button>银行卡</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                <div>
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/9c2eaba940d92edb804e4654664f7072.png" alt="">
                    <button>信用卡</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
                <div>
                    <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/08/02/35555e3740d3421d800a0e34f4b5f4f5.png" alt="">
                    <button>VISA</button>
                    <Icon class="icon" type="ios-arrow-dropright" size='25' />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Navtop from '../components/navtop'

export default {
    name:"price",
    data(){
        return{
            pric : 0,
            cite:'',
            mail:'',
            name:'',
            pho:''
        }
    },
    mounted(){
        this.price();
        this.cites();
    },
    methods:{
        cites(){
            let admin = sessionStorage.getItem('isLogin');
            var GameScore = Bmob.Object.extend("User_cite"+admin);
            var query = new Bmob.Query(GameScore);
            let self = this;
            query.find({
            success: (results)=> {
                for (var i = results.length-1; i >=0; i--) {
                var object = results[results.length-1];
                    this.name = object.get('name');
                    this.pho = object.get('phone');
                    this.cite = object.get('cite');
                    this.mail = object.get('mail');
                }
            },
            error: function(error) {
                alert('第一次进入，请设置你的个人信息');
                self.$router.push('place')
            }
            });
        },
        price(){
            let nums = this.$route.query.goodsnum.split(',')
            var GameScore = Bmob.Object.extend("goods");
            var query = new Bmob.Query(GameScore);
            query.find({
            success: (results)=> {
                let prices = 0;
                for (var i = 0; i <results.length; i++) {
                    var object = results[i];
                    prices += object.get('price')*nums[i];
                }
                this.pric = prices;
            },
            error: function(error) {
                
            }
            });
        },
        changeCite(){
            this.$router.push('place')
        }
    },
    components:{
        Navtop
    }
}
</script>

<style scoped>
.btn{
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    margin-top: 10px;
}
.btn button{
    position: relative;
    left: 10px!important;
}
.price p,.cite p{
    font-size: 15px;
    width: 150px;
    color: gray;
}
.cite,.price{
    position: relative;
}
.cite h4,.price h4{
    font-size: 18px;
}
h3{
    font-size: 22px;
}
.price span,.cite span{
    position: absolute;
    left: 150px;
    color: black;
}
.pay div{
    height: 50px;
    position: relative;
    border-bottom: 1px solid rgb(230, 218, 218);
}
.cite div{
    height: 50px;
    position: relative;
}
.ma{
    height: calc(100% - 64px);
     background: rgb(250, 241, 241);
}
.ma>div{
    width: 94%;
    margin-left: 3%;
    border: 1px solid rgb(230, 218, 218);
    background: white;
    padding: 10px;
}
.ma button:not(.btn){
    outline: none;
    text-align: left;
    position: absolute;
    left: 40px;
    width: 92%;
    line-height: 40px;
    border: none;
    background: white;
    font-size: 15px;
}
.pay img{
    margin-top: 5px;
    width: 35px;
}
.icon{
    position: absolute;
    left: 90%;
    line-height: 50px
}

</style>



