<template>
    <div class="cr">    
    <ul class="menu-list">
        <li v-for="(src,index) in links" :key="index" class="item">
            <Icon class="icon" :type="src.icon" color=white size=20></Icon>
            <router-link :to ='src.href'>{{src.title}}</router-link>
        </li>
    </ul>
</div>   
</template>

<script>
export default {
    name:"",
    data(){
        return{
            links:[
                {title:'主页',href:"/" ,icon:"ios-home-outline"},
                {title:'分类',href:"/categorize",icon:"ios-color-filter-outline"},
                {title:'购物车',href:"/shopcar",icon:"ios-cart-outline"},
                {title:'我',href:"/personal",icon:"ios-person-add-outline"}
            ]
        }
    },
    methods:{
    }
}
</script>

<style scoped>
.cr{
    height: 49px;
    position: relative;
    background: #333;
    
}

.menu-list .item{
    list-style: none;
    width: 25%;
    height: 49px;
    float: left;
    background: transparent;
    position: relative;
    cursor: pointer;
    padding: 3px 0;
}
.item a{
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: white;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    z-index: 1;
}
li .icon{
    margin-bottom: 15px;
    position: absolute;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
    size:50px
}
</style>



