// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import store from './store'

import iView from 'iview';
import 'iview/dist/styles/iview.css';
import {
  Carousel,
  CarouselItem,
  Tabs,
} from 'element-ui'
import './assets/js/key'

Vue.config.productionTip = false
Vue.use(iView);
Vue.use(Vuex)
Vue.use(Carousel)
Vue.use(Tabs)
Vue.use(CarouselItem)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

router.beforeEach((to,from,next)=>{
  switch(to.name){
    case 'details':{
      store.commit('changeTitle','详情')
      }break;
      case 'mian':{
      store.commit('changeTitle','主页')
      }break;
      case 'shopcar':{
      store.commit('changeTitle','购物车')
      }break;
      case 'categorize':{
      store.commit('changeTitle','分类')
      }break;
      case 'personal':{
      store.commit('changeTitle','个人中心')
      }break;
      case 'price':{
      store.commit('changeTitle','结算中心')
      }break;
      case 'login':{
      store.commit('changeTitle','登录/注册')
      }break;
      case 'place':{
      store.commit('changeTitle','收货地址')
      }break;
      case 'set':{
      store.commit('changeTitle','个人设置')
      }break; 
}
  next();
})

export default router;



