<template>
    <div class="content">
        <Navtop />
        <div class="ma">
            <div>
                <button @click="change" class="cha">编辑</button>
            </div>
            <div class="flx">
                   <div v-for="index in 4" :key="index" v-show="dele[index-1]">
                       <button @click="deleted" type="button" :class="indexs[index-1]" v-show="non">删除</button>
                       <img :src="img[index-1]" alt="">
                       <p>{{title[index-1]}}</p>
                       <small>单价:{{price[index-1]}}</small>
                       <div class="nums" v-show="isnum">
                           <span @click="remo" :class="indexs[index-1]">-</span>
                           <input type="number" min="0" v-model.number="num[index-1]">
                           <span @click="add" :class="indexs[index-1]">+</span>
                       </div>
                    </div>
               </div>
               <button type="button" class="btn" @click="goSell">前往支付</button>
        </div>
        <Navlist />
    </div>    
</template>

<script>
import Navlist from '../components/navList'
import Navtop from '../components/navtop'
import Vue from 'vue'

export default {
    name:"",
    mounted(){
        this.downdate();
        this.isLogin();
    },
    data(){
        return{
            indexs:'1234',
            is:true,
            img:[null],
            title:[null],
            price:[null],
            num:[null],
            dele:[true,true,true,true],
            non:false,
            isnum:true
        }
    },
    methods:{
         isLogin(){
                if(!(sessionStorage.isLogin)){
                    this.$router.push({
                    name:'login',
                })
            }
        },
        add(e){
            let inde = e.path[0].className-1;
            let nums = this.num[inde];
            nums++;
            if(this.num[inde]>0){
                Vue.set(this.num,inde,nums);
            }
        },
        remo(e){
            let inde = e.path[0].className-1;
            let nums = this.num[inde];
            nums--;
            if(this.num[inde]>0){
                Vue.set(this.num,inde,nums);
            }
        },
        deleted(e){
            let inde = e.path[0].className-1;
            Vue.set(this.dele,inde,false);
        },
        change(){
            this.non = !this.non;
            this.isnum = !this.isnum
        },
        goSell(){
            this.$router.push({
                name:"price",
                query:{goodsnum:this.num.join()}
                });
        },
         downdate(){
            var Goods = Bmob.Object.extend("goods");
            var query = new Bmob.Query(Goods);
            query.find({
            success: (results) =>{
                let imgstr = [];
                let titlestr = [];
                let pricestr  = [];
                let numstr = [];
                for (var i = 0; i < results.length; i++) {
                    var object = results[i];
                    imgstr.push(object.get('imgurl'));
                    titlestr.push(object.get('name'));
                    pricestr.push(object.get('price'));
                    numstr.push(object.get('num'))
                }
                //这里赋值
                this.img= imgstr;
                this.title = titlestr;
                this.price = pricestr;
                this.num = numstr;
            },
            error: function(object, error) {
                alert("购物车为空，请前往首页选购");
            }
            });
        }
    },
    components:{
        Navlist,
        Navtop
    }
}
</script>

<style scoped>
.flx div:not(.nums){
position: relative;
    width: 95%;
    height: 100px;
    clear: both;
    border-bottom: 1px solid rgb(204, 198, 198);   
    margin: 15px 2%;
    background: white
}
.cha{
    outline: none;
    margin:10px 0 0 85%;
    border: none;
    background:rgb(243, 241, 241);
    color: rgb(44, 44, 150);
    font-size: 18px;
}
.flx>div>button{
    position: absolute;
    left: 80%;
    bottom: 10px;
    width: 70px;
    height: 40px;
    border: none;
    background: rgb(129, 25, 25);
    color: white;
    border-radius: 5px
}
.ma{
    background: rgb(243, 241, 241);
}

.flx img{
    width: 90px;
    height:auto;
    float: left;
}
.flx p{
    font-size: 15px;
    color: rgb(75, 75, 139);
    position: absolute;
    left: 120px;
    top: 20px;;
}
.flx small{
    font-size: 18px;
    color: rgb(100, 77, 35);
    position: absolute;
    left: 120px;
    bottom: 10px;
}
.nums{
    position: absolute;
    left: 70%;
    bottom: 10px;
    font-size: 0;
}
.nums input{
    font-size: 15px;
    width: 30px;
    height: 20px;
    border: none;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    text-align: center;
}
.nums span{
    text-align: center;
    font-size: 15px;
    display: inline-block;
    line-height: 20px;
    width: 30px;
    height: 20px;
    border: 1px solid gray;
    transform: translateY(4%);
}
.btn{
    margin-top: 20px;
    width: 80%;
    height: 45px;
    background: orange;
    color: white;
    font-size: 18px;
    outline: none;
    border: none;
    border-radius: 10px;
    margin-left: 10%;
}
</style>



