import  Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isLogin:true,
        title:"主页",
    },
    mutations: {
        changeTitle(state,title){
          state.title = title;
        }
    }
  });

  


