<template>
    <div class="content">
        <Navtop />
        <div class="ma">
           <header>
               <el-carousel height='150px'  :interval="5000" arrow="always">
                    <el-carousel-item class="flashimg" v-for="item in imgSrc" :key="item">
                    <img :src="item" alt="">
                    </el-carousel-item>
                </el-carousel>
                <div class="hd-t">
                    <Icon type="ios-alert-outline" size='20' color='orange'/>
                    <span>7夕期间订单可能较多，请提前预定</span>
                </div>
           </header>
           <main>
               <h3>新品上市</h3>
               <div class="newflower flx">
                   <div v-for="index in 4" :key="index" class="newd" @click='goDetail'>
                       <img :src="img[index-1]" alt="">
                       <p>{{title[index-1]}}</p>
                       <small>价格:{{price[index-1]}}</small>
                    </div>
               </div>
                <h3>当季热销</h3>
                <div class="hotflower flx">
                   <div v-for="index in 4" :key="index" class="newd">
                       <img :src="img[index-1]" alt="">
                       <p>{{title[index-1]}}</p>
                       <small>价格:{{price[index-1]}}</small>
                    </div>
               </div>
               <h3>活动特卖</h3>
               <div class="activeflower flx">                  
                   <div v-for="index in 4" :key="index" class="newd">
                       <img :src="img[index-1]" alt="">
                       <p>{{title[index-1]}}</p>
                       <small>价格:{{price[index-1]}}</small>
                    </div>
               </div>
               <div v-show="details" class="details">
                   <p @click="moveDetails" class="remo">X</p>
                   <hr>
                   <div class="detama">
                       <img src="http://bmob-cdn-19599.b0.upaiyun.com/2018/07/30/c22c26e240c2ce1c80785e5bbbcecc16.jpg" alt="" width="40%" height="200px">
                       <p>花名:    情巴黎----33枝红玫瑰</p>
                       <p>规格:    戴安娜玫瑰66枝 勿忘我适量围绕</p>
                       <p>包装:    内层粉色厚棉纸 外层银灰色短平板印花树叶草 粉紫双色缎带</p>
                       <p @click='goDetails' class="godeta">详情></p>
                   </div>
                   <div class="detatext">
                       <br>
                       <h3>不变的心----戴安娜玫瑰66枝</h3>
                       <p>你的温柔似水，静静无言，用心浇灌一份爱，等待最美的时刻绽放。</p>
                       <small class="sma"><span>￥298</span><span>库存:10</span></small>
                       <div class="btn">
                        <button>加入购物车</button>
                        <button>立即购买</button>
                       </div>
                   </div>
                   
               </div>
           </main>
        </div>
        <Navlist />
    </div>
</template>

<script>
import Navlist from '../components/navList'
import Navtop from '../components/navtop'

export default {
    mounted(){
        this.downdate();
    },
    name:"mian",
    methods:{
        goDetails(){
            this.$router.push({
                name:'details',
                query:{msg:this.val}
            })
        },
        moveDetails(){
            this.details = !this.details;
        },
        goDetail(){
            this.details = !this.details;
        },
        downdate(){
            Bmob.initialize("0319b91e2178a41cbd7c7683126ed4cd", "3afd32e8095f62c049de343910a9473f");
            var Goods = Bmob.Object.extend("goods");
            var query = new Bmob.Query(Goods);
            query.find({
            success: (results) =>{
                let imgstr = [];
                let titlestr = [];
                let pricestr  = [];
                for (var i = 0; i < results.length; i++) {
                    var object = results[i];
                    imgstr.push(object.get('imgurl'));
                    titlestr.push(object.get('name'));
                    pricestr.push(object.get('price'));
                }
                //这里赋值
                this.img= imgstr;
                this.title = titlestr;
                this.price = pricestr;
            },
            error: function(object, error) {
                alert("query object fail");
            }
            });
        }
    },
    data(){
        return{
            val:'详情',
            imgSrc:['http://bmob-cdn-19599.b0.upaiyun.com/2018/07/30/15fdf90040e86d3c8005ea2e0d1ab4f1.jpg','http://bmob-cdn-19599.b0.upaiyun.com/2018/07/30/f76b58c340d3c15c80196045f1e30962.jpg','http://bmob-cdn-19599.b0.upaiyun.com/2018/07/30/23962283408a5bc6800a76fe017e42d7.jpg'],
            img:[null],
            title:[null],
            price:[null],
            details:false
        }
    },
    components:{
        Navlist,
        Navtop
    }
}
</script>

<style scoped>
.flx div{
    margin-bottom: 10px;
}
.flashimg img{
    height: 100%;
}
.el-carousel__arrow {
    transform: translateY(-100px)!important;
}
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .hd-t{
      border-bottom: 1px solid rgb(201, 196, 196);
      padding:10px;
      text-align: center;
      line-height: 100%;
  }
  .flx{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  main h3{
      margin: 20px 0 0 7%;
      font-size: 20px;
  }
  .newd{
      width: 40%;
      height: 300px;
      position: relative;
  }
  .newd img{
      width: 100%;
      height: 200px;;
  }
  .newd small{
      position: absolute;
      bottom: 0;
      color: rgb(177, 120, 14);
      font-size: 15px;
  }
  header{
      z-index: -1;
  }
.details{
      position: fixed;   
      background: white;
      height: 90%;
      width: 90%;
      left: 5%;
      top: 5%;
      z-index: 999;
      border: 1px solid gray;
  }
  .detama{
      height: 250px;
      clear: both;
  }
  .detama img{
      float: left;
      margin: 20px;
      border: 1px solid rgb(199, 196, 196);
  }
  .detama p{
      padding: 13px 0;
      margin-right: 15px;
      font-size: 12px
  }
  .detatext{
      width: 100%;
      height: calc(100% - 250px - 38px);
      position: relative;
  }
  .details fieldset{
      margin-top: 50px;
  }
  .detatext p{
      text-indent: 2em;
      margin: 15px;
  }
  .godeta{
      text-align: right;
      margin-right: 20px;
      color: #99a9bf;
  }
  .remo{
      padding: 10px ;
      text-align: right;
  }
.btn{
    display: block;
    margin-left: 5%;
    position: absolute;
    bottom: 20px;
    width: 100%;
}
.btn button{
    width: 45%;
    line-height: 50px;
    font-size: 15px;
    background: white;
    border: 1px solid black;
    border-radius: 5px;
    outline: none;
}
.sma{
    border-top:1px solid rgb(190, 188, 188) ;
}
small{
    width: 100%;
    line-height: 50px;
    border-bottom:1px solid rgb(190, 188, 188) ;
    position: absolute;
    bottom: 100px;
}
small span{
    display: inline-block;
    width: 50%;
    text-align: center;
    font-size: 20px;
}
small span:not(:first-child){
    border-left: 1px solid rgb(190, 188, 188);
}
</style>



