<template>
    <div class="navigationbar">
        <Icon class="goBtn" @click = 'goBackClick' type="ios-arrow-back" v-if="goBack" color=white size=24 />
        <h3 class="title">{{this.$store.state.title}}</h3>
    </div>
</template>


<script>
export default {
    name:"navtop",
    data(){
        return{
            title:'XXXX'
        }
    },
    props:{
        goBack:{
            type:Boolean,
            default:false
        }
    },
    methods:{
        goBackClick(){
            this.$router.go(-1);
        }
    },
    watch:{
        $route(to,from){
        switch(to.name){
            case 'details':{
            this.$store.commit('changeTitle','详情')
            }break;
            case 'mian':{
            this.$store.commit('changeTitle','主页')
            }break;
            case 'shopcar':{
            this.$store.commit('changeTitle','购物车')
            }break;
            case 'categorize':{
            this.$store.commit('changeTitle','分类')
            }break;
            case 'personal':{
            this.$store.commit('changeTitle','个人中心')
            }break;
            case 'price':{
            this.$store.commit('changeTitle','结算中心')
            }break;
            case 'login':{
            this.$store.commit('changeTitle','登录/注册')
            }break;
            case 'place':{
            this.$store.commit('changeTitle','收货地址')
            }break;
            case 'set':{
            this.$store.commit('changeTitle','个人设置')
            }break;  
        }
        }
    },  
}
</script>

<style scoped>
.navigationbar{
    background: black;
    color: #fff;
    height: 64px;
    position: relative;
}
.navigationbar .title{
    text-align: center;
    line-height: 64px;
    letter-spacing: 5px;
    font-size:22px;
    font-weight: lighter;
}
.goBtn{
    size:30px;
    color: white;
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%)
}
</style>



