import Vue from 'vue'
import Router from 'vue-router'

import Mian from '@/pages/mian'
import Sets from '@/pages/set'
import Categorize from '@/pages/categorize'
import Shopcar from '@/pages/shopcar'
import Details from '@/pages/details'
import Login from '@/pages/login'
import Personal from '@/pages/personal'
import Place from '@/pages/place'
import Price from '@/pages/price'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mian',
      component: Mian
    },
    {
      path: '/details',
      name: 'details',
      component: Details
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/personal',
      name: 'personal',
      component: Personal
    },
    {
      path: '/place',
      name: 'place',
      component: Place
    },
    {
      path: '/set',
      name: 'set',
      component: Sets
    },
    {
      path: '/shopcar',
      name: 'shopcar',
      component: Shopcar
    }
    ,{
      path: '/categorize',
      name: 'categorize',
      component: Categorize
    },
    {
      path: '/price',
      name: 'price',
      component: Price
    }
  ]
})



