<template>
    <div class="content">
        <Navtop />
        <div class="ma">
            <div class="btn">
                <button type="button" v-bind:class="{ sty: isLg }" @click="goLogin">登录</button>
                <button type="button" v-bind:class="{ sty: isRg }" @click="goRegister">注册</button>
            </div>
            <div class="lgma">
                <div class="login" v-show="lg">
                    <form action="">
                        <div class="setico">
                            <Icon type="ios-body-outline" class="ico" size='30' />
                            <input v-model="lgAd" type="text" placeholder="请输入账号">
                        </div>
                        <div class="setico">
                            <Icon type="md-key" class="ico" size='30'  />
                            <input v-model="lgPw" type="password" placeholder="请输入密码">
                        </div>
                        <button type="button" @click="login">登录</button>
                    </form>
                </div>
                <div class="register" v-show="rg">
                    <form action="">
                        <div class="setico">
                            <Icon type="ios-body-outline" class="ico" size='30'  />
                            <input v-model="rgAd" type="text" placeholder="请输入账号">
                        </div>
                        <div class="setico">
                            <Icon type="md-key" class="ico" size='30'  />
                            <input v-model="rgPw" type="password" placeholder="请输入密码">
                        </div>
                        <div class="setico">
                            <Icon type="ios-key-outline" class="ico" size='30'  />
                            <input type="password" v-model="isPw" placeholder="请重复密码">
                        </div>
                        <div class="setico">
                            <Icon type="ios-mail-outline" class="ico" size='30'  />
                            <input v-model="pho" type="text" placeholder="请输入邮箱">
                        </div>
                        <button type="button" @click="register">注册</button>
                    </form>
                </div>
            </div>
        </div>
    </div>    
</template>

<script>
import Navtop from '../components/navtop'

export default {
    name:"",
    methods:{
        goLogin(){
            if(!this.lg){
                this.lg = true;
                this.rg = false;
                this.isLg = true;
                this.isRg = false;
            }
        },
        goRegister(){
            if(!this.rg){
                this.rg = true;
                this.lg = false;
                this.isRg = true;
                this.isLg = false;
            }
        },
        login(){
            let self = this;
            let admin = this.lgPw;
            if(this.lgAd && this.lgPw){
                Bmob.User.logIn(this.lgAd, this.lgPw, {
                    success: function(user) {
                        sessionStorage.isLogin = admin;
                        self.$router.push({
                            name:'mian',
                        })
                    },
                    error: function(user, error) {
                        // The login failed. Check error to see why.
                    }
                });
            }else{
                alert('请输入用户名或密码');
            }
        },
        register(){
            let self = this;
            let admin = this.rgPw;
            
            var user = new Bmob.User();
            user.set("username", this.rgAd);
            user.set("password", this.rgPw);
            user.set("email", this.pho);
            if((this.rgAd&&this.rgPw)&&this.rgPw==this.isPw){
                user.signUp(null, {
                success: function(user) {
                    sessionStorage.isLogin = admin;
                    self.$router.push({
                        name:'mian',
                    })
                },
                error: function(user, error) {
                    // Show the error message somewhere and let the user try again.
                    alert("Error: " + error.code + " " + error.message);
                }
                });
            }else{
                alert('请输入正确的信息')
            }
        }
    },
    data(){
        return{
            lg:true,
            rg:false,
            isLg:true,
            isRg:false,
            lgAd:'',
            lgPw:'',
            rgAd:'',
            rgPw:'',
            pho:'',
            isPw:''
        }
    },
    components:{
        Navtop,
    }
}
</script>

<style scoped>
.btn{
    width: 100%;
    line-height: 40px;
    border-bottom: 1px solid black;
    box-sizing: border-box;
    font-size: 0;
}
.sty{
    color: green;
    border-bottom: 5px solid green!important;
}
.btn button{
    width: 50%;
    font-size: 15px;
    text-align: center;
    background: white;
    border: none;
    outline: none;
}
.login,.register{
    text-align: center;
}
.login input,.register input{
    width: 95%;
    margin: 40px auto;
    height: 50px;;
    outline: none;
    border: none;
    border-bottom:1px solid gray; 
    display: block;
    font-size: 18px;
    text-indent: 2em
}
.login button,.register button{
    width: 95%;
    line-height: 45px;
    border-radius: 10px;
    background: green;
    border: none;
    color: white;
    font-size: 20px;
    outline: none;
}
.setico{
    position: relative;
}
.ico{
    position: absolute;
    left: 15px;
    top: 10px;
}
</style>



